
body{
  margin: 0 40px;
}
#app {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-family: 'Source Sans Pro';
  font-size: 20px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0 auto;
  max-width: 1280px;
  min-height: 100vh;
}
#logo {
  display: block;
  margin-bottom: 2em;
  max-width: 300px;
  width: 100%;
}
p {
  color: #999999;
  font-size: 25px;
  margin: 0 0 4em 0;
  text-align: center;
  word-break: break-word;
}
p.excuse{
  color: #002664;
}
a {
  color: #999999;
  margin: 1em 0;
  text-decoration: none;
  -webkit-transition: 200ms all;
  transition: 200ms all;
}
a:hover {
  color: #002664;
}
.button {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #f8fae4;
  background-image: url(data:image/png;base64,);
  border-radius: 5px;
  -webkit-box-shadow: 4px 4px 30px 0px darkgrey;
          box-shadow: 4px 4px 30px 0px darkgrey;
  color: #000;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-family: 'Press Start 2P';
  font-size: 9px;
  height: 85px;
  margin: 0;
  overflow: hidden;
  padding: 0 4em;
  position: relative;
  text-align: center;
  text-decoration: none;
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}
.button::before{
  background-color: #fc4732;
  content: '';
  display: block;
  height: 17px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.button::after{
  background-color: #fc4732;
  bottom: 0;
  content: '';
  display: block;
  height: 17px;
  left: 0;
  position: absolute;
  width: 100%;
}
.button:hover {
  color: #000;
}
.button:hover span::before {
  display: block;
}
.button span::before {
  background-color: #fff;
  border-radius: 50%;
  -webkit-box-shadow: inset 1px 0px 2px 0px black;
          box-shadow: inset 1px 0px 2px 0px black;
  content: '';
  display: none;
  height: 10px;
  position: absolute;
  right: 7px;
  top: 12px;
  width: 10px;
}
.octocat {
  position: fixed;
  top: 20px;
  right: 20px;
  margin: 0;
}
@media (min-width: 960px) {
body {
    margin: 0;
}
#logo {
    max-width: none;
    width: auto;
}
p {
    font-size: 80px;
    margin-bottom: 1em;
}
a {
    font-size: 24px;
}
.button {
    font-size: 18px;
    height: 175px;
    padding: 0 4em;
}
.button:hover {
    -webkit-transform: translateY(-10px) rotate(3deg);
            transform: translateY(-10px) rotate(3deg);
}
.button::before {
    height: 35px;
}
.button::after{
    height: 35px;
}
.button span::before {
    -webkit-box-shadow: inset 2px 1px 5px 0px black;
            box-shadow: inset 2px 1px 5px 0px black;
    height: 20px;
    right: 15px;
    top: 25px;
    width: 20px;
}
}
